.habit {
  height: 200px;
  width: 200px;
  background: #fff;
  border-radius: 20px;
  padding: 10px 30px;
  margin: 20px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -webkit-transition: all 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: all 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: all 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.habit:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}

.habit__delete {
  position: absolute;
  top: 15px;
  right: 15px;
  color: #ccc;
}

.habit__delete:hover {
  color: #777;
}

.habit h1 {
  position: absolute;
  bottom: 73px;
}

.habit h2 {
  width: 175px;
  height: 55px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.habit h2,
.habit h4 {
  font-weight: 400;
}

.habit h4 {
  font-size: 2.5em;
  margin: 0;
  position: absolute;
  bottom: 25px;
  left: 30px;
}

/* ======= PROGRESS BAR ======= */
.progress {
  position: absolute;
  top: 40px;
  right: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.progress__bar-outer,
.progress__bar-inner {
  height: 125px;
  width: 20px;
}

/* Bottom */
.progress__bar-outer {
  border-radius: 15px;
  background: -webkit-gradient(linear, left top, left bottom, from(#7b4397), to(#dc2430));
  background: -webkit-linear-gradient(top, #7b4397, #dc2430);
  background: -o-linear-gradient(top, #7b4397, #dc2430);
  background: linear-gradient(to bottom, #7b4397, #dc2430);
  overflow: hidden;
}

/* Top */
.progress__bar-inner {
  background: #ddd;
}

.progress__btn {
  border: 0;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  background: #eee;
  font-size: 0.8em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 5px;
  outline: none;
  -webkit-transition: all 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
  -o-transition: all 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: all 200ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.progress__btn:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}



